.prtfolio{
  height: 100vh;
}

.portfolioList {
    display: flex;
  }
  
  h2{
    color: var(--txt-color);
    text-align: center;
    padding-bottom: 19px;
    margin-bottom: 19px;
    margin-top: 19px;
    font-weight: 700;
  }

  .card {
    display: flex;
    padding-right: 20px;
    text-decoration: none;
    color: var(--text-color);
    margin: 20px 0  20px 0;
  }
  
  .card img {
    min-width: 150px;
    height: 150px;
    object-fit: cover;
  }
  @media screen and (max-width: 1100px) {
    .card img {
      min-width: 100px;
      height: 100px;
    }
  }
  
  .cardText {
    margin-left: 20px;
  }

  .repo{
    margin: 10px 0 20px 34px;
    margin-top: 40px !important;
    text-decoration: none !important;
    outline: none !important;
    transition: 0.5s;
  }

  .repo:hover{
    cursor: pointer;
    color: grey;
    transform: scale(1.1);
  }